@media screen and (max-width: 1170px) {
  .sectionInner {
    width: 100%;
    padding: 0 20px;
  }
}
@media screen and (max-width: 1100px) {
  .varwinInBusiness_lineBlock,
.varwinOpportunitiesLine {
    grid-template-columns: repeat(2, 370px);
    justify-content: center;
  }

  .varwinTariffs_tarifs {
    width: 100%;
  }
}
@media screen and (max-width: 992px) {
  .varwinImplementation .sectionInner {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .varwinImplementation_text {
    align-self: center;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 0 20px 0;
  }

  .varwinImplementation_cards {
    width: 100%;
    grid-template-columns: repeat(2, 320px);
    justify-content: center;
  }
}
@media screen and (max-width: 880px) {
  .varwinEduBanner_img,
.varwinLicenseBanner_img {
    display: none;
  }

  .varwinLicenseBanner_textWrapper,
.varwinEduBanner_textWrapper {
    width: 100%;
    align-items: center;
  }

  .varwinTariffs_tarifs {
    flex-direction: column;
  }

  .varwinTariffs_tarifs-tarif {
    margin: 0 0 20px 0;
    width: 320px;
  }
}
@media screen and (max-width: 780px) {
  .sectionTitle,
.varwinTariffs_title {
    width: 100%;
    text-align: center;
    font-size: 24px;
    line-height: 1.5;
  }

  .varwinEduBanner_textWrapper-title,
.varwinLicenseBanner_textWrapper-title,
.varwinImplementation_text-title,
.formTitle {
    text-align: center;
    font-size: 24px;
    line-height: 1.5;
  }

  .varwinImplementation_text-text,
.varwinTariffs_text {
    font-size: 18px;
  }

  .varwinInBusiness_lineBlock,
.varwinOpportunitiesLine {
    grid-template-columns: 320px;
  }

  .varwinImplementation_cards {
    grid-template-columns: 320px;
  }

  .rew_itemslide {
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
  }

  .rew_itemslide .rew_userInfo {
    margin: 0 0 20px 0;
  }

  .form .row1 {
    flex-direction: column;
  }

  .form .col1,
.form .col2 {
    width: 100%;
  }
}